.header {
  grid-area: header;
  background-color: #fff;
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.logo {
  width: 50px;
  height: auto;
  margin-left: 10px;
  margin-top: 5px;
}

.loginBlock {
  text-align: right;
  padding-top: 20px;
  padding-right: 10px;
}

.loginBlock a {
  color: #333;
  text-decoration: none;
  border-radius: 5px;
  padding: 5px 10px;
  box-shadow: inset 0px 1px 10px 0px rgb(85 85 85 / 20%);
}

.rightInfo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.rightInfo button {
  background: none;
  border: none;
  cursor: pointer;
}

.userLogin {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin-right: 15px;
}

.userEmail {
  opacity: 0.7;
}
